import { Typography } from '@mui/material';
import * as React from 'react';

import HomepageHero from '../components/HomepageHero';
import HomepageUserSelection from '../components/HomepageUserSelection';
import { LandlordUseSummary, TenantUseSummary, TradeUseSummary } from '../components/UseSummary';

export default function Home() {
    return (
        <div>
            <HomepageHero />
            <HomepageUserSelection />
            <Typography variant="h2">
                What we can do for you
            </Typography>
            <LandlordUseSummary />
            <TradeUseSummary />
            <TenantUseSummary />
        </div>
    )
}